import { Component, Prop, Event, EventEmitter, h, Host } from '@stencil/core';

export type ModalActionsType = 'fill' | 'right-aligned';

@Component({
  tag: 'bh-modal-actions',
  styleUrl: 'bh-modal-actions.css',
  shadow: true,
})
export class BhModalActions {
  /**
   * Layout type for the actions:
   * - 'fill': Buttons take full width
   * - 'right-aligned': Buttons aligned to the right with optional left content slot
   */
  @Prop() type: ModalActionsType = 'fill';

  /**
   * Whether to show destructive styling on primary button
   */
  @Prop() destructive: boolean = false;

  /**
   * Primary/destructive action button label
   */
  @Prop() primaryLabel: string = 'Confirm';

  /**
   * Secondary action button label
   */
  @Prop() secondaryLabel: string = 'Cancel';

  /**
   * Whether to show the divider line above actions
   */
  @Prop() divider: boolean = false;

  /**
   * Emitted when the primary/destructive action button is clicked
   */
  @Event() bhPrimaryAction!: EventEmitter<void>;

  /**
   * Emitted when the secondary action button is clicked
   */
  @Event() bhSecondaryAction!: EventEmitter<void>;

  private handlePrimaryAction = (): void => {
    this.bhPrimaryAction.emit();
  };

  private handleSecondaryAction = (): void => {
    this.bhSecondaryAction.emit();
  };

  private renderButtons() {
    const isRightAligned = this.type === 'right-aligned';

    const primaryButton = this.destructive ? (
      <button
        class="btn-destructive"
        onClick={this.handlePrimaryAction}
        type="button"
      >
        {this.primaryLabel}
      </button>
    ) : (
      <bh-button
        hierarchy="primary"
        icon="none"
        size="md"
        label={this.primaryLabel}
        onBhClick={this.handlePrimaryAction}
      />
    );

    const secondaryButton = (
      <bh-button
        hierarchy="secondary"
        icon="none"
        size="md"
        label={this.secondaryLabel}
        onBhClick={this.handleSecondaryAction}
      />
    );

    if (isRightAligned) {
      return (
        <div class="modal-actions-content modal-actions-content--right">
          <div class="actions-left">
            <slot name="left-content" />
          </div>
          <div class="actions-right">
            {secondaryButton}
            {primaryButton}
          </div>
        </div>
      );
    }

    // Full width buttons (stacks vertically on mobile via CSS)
    return (
      <div class="modal-actions-content modal-actions-content--fill">
        {secondaryButton}
        {primaryButton}
      </div>
    );
  }

  render() {
    return (
      <Host>
        <div class="modal-actions">
          {this.divider && <div class="divider" />}
          {this.renderButtons()}
        </div>
      </Host>
    );
  }
}
